<template>
  <div
    id="image-background"
    :style="{ backgroundImage: `url(${backgroundImage})` }"
  />
</template>

<script>
export default {
  data() {
    return {
      backgroundImage: 'assets/images/wallpaper/windows-11-stock-grey-abstract-dark-background-3840x2160-8957.jpg'
    };
  },
  mounted() {
    this.loadImage();
  },
  methods: {
    async loadImage() {
      try {
        // 先使用默认图片
        this.setImage(this.backgroundImage);

        // 从 Unsplash API 获取随机图片 URL
        const response = await fetch('https://source.unsplash.com/random/1920x1080');
        const unsplashImageUrl = response.url;

        // 创建一个 Image 对象，确保图片完全加载后再更新样式
        const img = new Image();
        img.src = unsplashImageUrl;
        img.onload = () => {
          this.setImage(unsplashImageUrl);
        };
        img.onerror = () => {
          console.error('获取 Unsplash 图片失败，仍使用默认图片。');
        };
      } catch (error) {
        console.error('获取图片失败:', error);
      }
    },
    setImage(url) {
      this.backgroundImage = url;
      document.getElementById('image-background').classList.add('loaded');
    }
  }
};
</script>

<style scoped>
#image-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  background-size: cover;
  background-position: center;
  transition: opacity 0.5s ease-in-out;
  opacity: 0; /* 初始透明度 */
}

#image-background.loaded {
  opacity: 1; /* 加载完成后完全不透明 */
}
</style>
